import { useState } from "react"
export const Project = ({icon, name, tech}) => {

  const [showTech, setShowTech] = useState(false)
  const [showHide, setShowHide] = useState('Show')

  const handleShowTech = () => {
    if(showTech){
      setShowHide('Show')
    } else setShowHide('Hide')

    setShowTech(!showTech)
  }

  return(
    <div className="p-5">
      <a className="hover:text-white" href="" target="_blank" rel="noreferrer">
        <div className="defaultIcon">{icon}</div>
        <p className="iconText">{name}</p>
      </a>
      <div className="ml-2">
        <div className="hover:text-white cursor-pointer" onClick={handleShowTech}>{`${showHide} Tech`}</div>
        <ul className="list-disc text-base ml-8">
          {showTech && tech.map(item => (<li key={item}>{item}</li>))}
        </ul>
      </div>
    </div>
  )
}