import { useState, useRef, useEffect } from 'react'
import emailjs from 'emailjs-com'

//https://dev.to/daliboru/how-to-send-emails-from-a-form-in-react-emailjs-27d1
//https://www.emailjs.com/docs/sdk/send-form/
//https://reginafurness.medium.com/emailjs-with-create-react-app-1f9c7f615c17

//TODO: reCAPTCHA? https://www.google.com/recaptcha/about/

//TODO: make each selection highlight if you miss a value

export const ContactMe = () => {
  const normalColor = 'text-techBlueLow'
  const alertColor = 'text-alertRed'
  const missingError = 'Please fill in the required fields'
  const otherError = 'An issue occured, try to send an email directly to: conorjameswarddeveloper@gmail.com'
  const userId = process.env.REACT_APP_USER_ID
  const serviceId = process.env.REACT_APP_SERVICE_ID
  const templateId = process.env.REACT_APP_TEMPLATE_ID
  const blankForm = {
    name:'',
    email:'',
    phone:'',
    subject:'',
    message:'',
  }
  
  const form = useRef()
  const [requireColor, setRequiredColor] = useState(normalColor)
  const [errorVisible, setErrorVisible] = useState('hidden')
  const [errorMessage, setErrorMessage] = useState(missingError)
  const [confirmationVisible, setConfirmationVisible] = useState('hidden')
  const[message, setMessage] = useState(blankForm)

  const [textWidth, setTextWidth] = useState(null)
  
  const calcTextWidth = () => {
    const sm = 640
    const md = 768
    const lg = 1024
    const xl = 1280

    const width = window.innerWidth

    if(width < sm){
      setTextWidth(30)
    } else if (width < md){
      setTextWidth(40)
    } else if (width < lg){
      setTextWidth(50)
    } else if (width < xl){
      setTextWidth(60)
    } else {
      setTextWidth(70)
    }
  }

  window.addEventListener('resize', calcTextWidth)

  useEffect(()=>{
    calcTextWidth()
  },[])

  const sendMessage = (e) => {
    e.preventDefault()
    if(!message.name || !message.email || !message.subject || !message.message){
      setRequiredColor(alertColor)
      setErrorVisible('block')
    } else {

      setRequiredColor(normalColor)
      setErrorVisible('hidden')

      emailjs.sendForm(serviceId, templateId, form.current, userId)
        .then((result) => {
          if(result.status === 200){
            setMessage(blankForm)
            setConfirmationVisible('block')
          }
        }, (error) => {
          setErrorVisible('block')
          setErrorMessage(otherError)
          console.log(error.text);
        })
    }
  }

  const handleChange = (e) => {
    setMessage({...message, [e.target.name]:e.target.value})
  }

  return (
    textWidth && 
    <div className="flex-auto text-techBlueLow border-b-2 mb-20">
      <div className="text-center p-10 text-xl">
        <p>Please feel free to reach out reguarding any questions or inqueries</p>
      </div>
      <div className={`text-center bg-alertBackground text-xl text-alertRed border-2 ${errorVisible}`}>{errorMessage}</div>
      <div className={`text-center text-xl bg-confirmationBackground text-confirmationGreen border-2 ${confirmationVisible}`}>Message Sent!</div>
      <form ref={form} className="xl:w-3/4 ml-auto mr-auto sm:px-2" onSubmit={sendMessage}>
        <div className="grid gap-4 grid-cols-1 md:grid-cols-2 lg:grid-cols-3">
          <div className="p-4 flex flex-col">
            <label className={`block ${requireColor}`} htmlFor="name">Name*</label>
            <input className="bg-techTextActive" id="name" type="text" name="name" onChange={handleChange} value={message.name}/>
          </div>
          <div className="p-4 flex flex-col">
            <label className={`block ${requireColor}`} htmlFor="email">Email*</label>
            <input className="bg-techTextActive" id="email" type="email" name="email" onChange={handleChange} value={message.email}/>
          </div>
          <div className="p-4 flex flex-col">
            <label className="block" htmlFor="phone">Phone</label>
            <input className="bg-techTextActive" id="phone" type="tel" name="phone" onChange={handleChange} value={message.phone}/>
          </div>
        </div>
        <div className="flex place-content-center p-6">
          <div>
            <div className="flex flex-col p-8">
              <div>
              <label className={`block ${requireColor}`}>Subject*</label>
              <input className="bg-techTextActive w-full" id="subject" type="text" name="subject" onChange={handleChange} value={message.subject}/>
              </div>
            </div>
            <label className={`block ${requireColor}`} htmlFor="message">Message*</label>
            <textarea className="bg-techTextActive" id="message" rows="10" cols={textWidth} name="message" onChange={handleChange} value={message.message}></textarea>
          </div>
        </div>
        <div className="p-10 pt-4 flex place-content-center">
          <input className="cursor-pointer
          bg-techText bg-fixed border-2 p-4 pl-10 pr-10 hover:text-techTextActive active:text-white" type="submit" value="Send Message"/>
        </div>
      </form>
    </div>
  )
}